.rating-backdrop {
  z-index: 100;
  background-color: var(--gray-title-w);
  transition: opacity var(--trns-modal), visibility var(--trns-modal);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.rating-backdrop.visible {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition-delay: .15s;
}

.rating-modal-container {
  z-index: 102;
  transition: all var(--trns-modal);
  transition-delay: .15s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.rating-backdrop.visible .rating-modal-container {
  transition-delay: 0s;
  transform: scale(.75)translate(-65%, -65%);
}

.rating-modal {
  background: var(--white);
  z-index: 999;
  border-radius: 15px;
  width: 100%;
  max-width: 335px;
  padding: 20px;
  position: relative;
}

